body{
    background-color: #041a4dbf;
    
     font-family: 'Roboto',sans-serif;
     height: 80%;
}
.messenger.theme{
    background-color: #041a4dbf;
    --text-color : #fff;
    --bg-color : #141b2bf0;
    --border-color : #19243bbf;
    --icon-bg-color: #0a0a1357;
    --active-color : #2b335591;
    --hover-color : #323a5c91;
    --fd-message-bg : #28324ed9;
    --my-message-bg : #28324ed9;
    --search-bg : #15162559;
    --text-white : #fff;
    --text-dark : #404040;
    --icon-color : #fff;
    --message-icon-color : #fff;
    --my-message-color : #fff;
    --fd-message-color : #fff;
    --active-icon-color : #5AD539;
    --image-border-color : #fff;
}
.messenger{
  width: 100%;
  height: 100vh;
  background-color: #ffffff;
  .row{
      height: 100%;
      .col-3{
          .left-side{
              padding:  5px 0px;
              border-right: 1px solid var(--border-color);
              display: flex;
              flex-direction: column;
              height: 100%;
              justify-content: start;
              .top{
                  display: flex;
                  flex-direction: row;
                  align-items: center;
                  justify-content: space-between;
                  margin: 12px 8px;
                  .image-name{
                      display: flex;
                      cursor: pointer;
                      .image{
                          width: 40px;
                          height: 40px;
                          border-radius: 50%;
                          overflow: hidden;
                          img{
                              width: 100%;
                              height: 100%;
                              object-fit: cover;
                          }
                      }
                      .name{
                          display: flex;
                          justify-content: center;
                          align-items: center;
                          h3{
                              padding-left: 10px;
                              font-weight: 600;
                              color: var(--text-color);
                          }
                      }
                  }
                  .icons{
                      display: flex;
                      position: relative;
                      .theme_logout.show{
                          transform: scale(1);
                      }
                      .theme_logout{
                          width: 200px;
                          background-color: rgb(20, 24, 59);
                          transform: scale(0);
                          position: absolute;
                          transition:  all .6s;
                          top: 100%;
                          padding: 10px;
                          color: #fff;
                          .on,.of{
                              label{
                                  cursor: pointer;
                                  color: #fff;
                              }
                              display: flex;
                              justify-content: space-between;
                              margin-top: 10px;
                          }
                          .logout{
                              display: flex;
                              align-items: center;
                              margin-top: 10px;
                              cursor: pointer;
                          }
                      }
                      .icon{
                          width: 33px;
                          height: 33px;
                          display: flex;
                          justify-content: center;
                          align-items: center;
                          background-color: var(--icon-bg-color);
                          margin-left: 8px;
                          border-radius: 24px;
                          color: var(--icon-color);
                          cursor: pointer;
                      }
                  }
              }
              .friend-search{
                  padding: 0px 8px;
                  .search{
                      display: flex;
                      width: 100%;
                      height: 40px;
                      background-color: var(--search-bg);
                      border-radius: 50px;
                      margin: 20px 0px;
                    //   color: var(--icon-color);
                        background-color: var(--icon-color);
                      button{
                          padding: 12px;
                          border-bottom-left-radius: 50px;
                          border-top-left-radius: 50px;
                          border: none;
                          color: var(--text-dark);
                          font-size: 20px;
                          background-color: transparent;
                      }
                      .form-control{
                          width: 100%;
                          padding-left: 0px;
                          border-bottom-right-radius: 50px;
                          border-top-right-radius: 50pc;
                          background-color: #fff0;
                          border: none;
                          color: var(--text-dark);
                          outline: none;
                          &::placeholder{
                            color:var(--text-dark);
                          }
                      }
                  }
              }
          }
      }
  }
} 